<template>
  <div :class="$style.content">
    <span
      v-for="(w, index) in widgetsAvailable"
      :key="`${index}-widget`"
      :class="[
        $style.link,
        w.disabled && $style.disabled,
      ]"
      @click="!w.disabled && select(index)">{{ w.name }}</span>
    <span
      v-for="(w, index) in teamLinks"
      :key="`${index}-external`"
      :class="[
        $style.link,
        $style.external,
      ]"
      @click="openLink(w.url)">{{ w.title }}</span>
    <mi-modal v-if="currentComponent" :title="currentComponent.name" v-model="show">
      <component :is="currentComponent.component" @refresh="() => { select(undefined); $emit('refresh'); }"></component>
    </mi-modal>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import types from '@/store/types';

import WidgetsIcon from 'vue-material-design-icons/WidgetsOutline.vue';

import Card from '@/components/Card';
import Picker from '@/components/Picker'
import Screams from '@/components/Screams'
import Memoriam from '@/components/Memoriam'
import Holidays from '@/components/Holidays'
import Links from '@/components/Links'
import MiModal from '@/components/Modal';

const widgetsAvailable = [
  { name: 'Chuasi', component: Picker, disabled: false },
  { name: 'Gritos', component: Screams, disabled: false },
  { name: 'In Memoriam', component: Memoriam, disabled: false },
  { name: 'Vacaciones', component: Holidays, disabled: false },
  { name: 'Links', component: Links, disabled: false },
];

export default {
  name: 'ButtonsBar',
  components: {
    Card,
    Picker,
    WidgetsIcon,
    MiModal,
  },
  data() {
    return {
      widgetsAvailable,
      selected: undefined,
      show: false,
    };
  },
  methods: {
    select(index) {
      this.selected = index;
      this.show = index != undefined;
    },
    openLink(url) {
      window.open(url, '_blank');
    },
  },
  computed: {
    ...mapGetters([
      types.getters.SELECTED_TEAM,
    ]),
    teamLinks() {
      return this[types.getters.SELECTED_TEAM].links ? this[types.getters.SELECTED_TEAM].links : [];
    },
    currentComponent() {
      if (this.selected !== undefined) {
        return widgetsAvailable[this.selected];
      }
    },
  },
};
</script>

<style module lang="scss">

.content {
  display: flex;
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
  transition: all 0.5s linear;
}
.link {
  cursor: pointer;
  display: flex;
  flex: 1;
  position: relative;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-primary-color-10);
  border: 2px solid var(--dark-primary-color-30);
  margin: 0 7px;
  padding: 7px 0;
  &:first-of-type {
    margin: 0 7px 0 0;
  }
  &:last-of-type {
    margin: 0 0 0 7px;
  }
  &:hover {
    background-color: var(--dark-primary-color-30);
  }
}
.disabled {
  cursor: not-allowed;
  &::after {
    content: 'ta roto :(';
    position: absolute;
    font-size: 0.8em;
    top: 0.1em;
    right: 0.1em;
    padding: 0 3px;
    background-color: var(--light-primary-color-20);
    line-height: 1em;
  }
  &:hover {
    background-color: var(--primary-color);
  }
}
.external {
  &::after {
    content: 'External';
    position: absolute;
    font-size: 0.8em;
    bottom: -3px;
    right: -3px;
    padding: 0 3px;
    background-color: var(--light-primary-color-20);
    line-height: 1em;
  }
}
</style>
