<template>
  <div :class="$style.content">
    <div
      v-for="day in weekdays" 
      :key="`${day}-day`"
      :class="$style.itemHeader">
      {{ day }}
    </div>
    <div
      v-for="day in month" 
      :key="`${day.date}-day`"
      :class="[
        $style.itemDay,
        moment(day.date, 'YYYY-MM-DD').isBefore(today, 'd') && $style.past,
        moment(day.date, 'YYYY-MM-DD').isSame(today, 'd') && $style.today,
        [0, 6].includes(moment(day.date, 'YYYY-MM-DD').day()) && $style.weekend,
      ]">
      <div :class="$style.dayNumber">{{ moment(day.date, 'YYYY-MM-DD').format('D') }}</div>
      <div :class="$style.dayCeremonies">
        <div
          v-for="(ceremony, i) in day.ceremonies" 
          :key="`${ceremony.name}-${i}`"
          :class="$style.dayCeremony">
          <img 
            :src="ceremony.selected ? members.find(x => x.id === ceremony.selected).img : info"
            :alt="ceremony.selected ? members.find(x => x.id === ceremony.selected).name : 'info'"/>
          <span :class="$style.ceremonyName">{{ ceremony.name }}</span>
        </div>
      </div>
      <div v-if="day.birthdates" :class="$style.dayBirthdays">
        <div
          v-for="(birthday, i) in day.birthdates" 
          :key="`${birthday.id}-${i}`"
          :class="$style.dayBirthday">
          <div :class="$style.hat">
            <img
              :src="members.find(x => x.id === birthday.id).img"
              :alt="members.find(x => x.id === birthday.id).name"/>
          </div>
          <span :class="$style.ceremonyName">Feliz Cumple!</span>
        </div>
      </div>
      <div v-if="day.anniversary" :class="$style.dayAnniversaries">
        <div
          v-for="(anniversary, i) in day.anniversary" 
          :key="`${anniversary.id}-${i}`"
          :class="$style.dayAnniversary">
          <div :class="$style.hat">
            <img
              :src="members.find(x => x.id === anniversary.id).img"
              :alt="members.find(x => x.id === anniversary.id).name"/>
          </div>
          <span :class="$style.ceremonyName">Feliz Aniversario!</span>
        </div>
      </div>
      <div v-if="day.holidays" :class="$style.dayAnniversaries">
        <div
          v-for="(holidays, i) in day.holidays" 
          :key="`${holidays}-${i}`"
          :class="$style.dayHolidays">
          <div :class="$style.hat">
            <img
              :src="members.find(x => x.id === holidays).img"
              :alt="members.find(x => x.id === holidays).name"/>
          </div>
          <span :class="$style.ceremonyName">Vacaciones</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapState } from 'vuex';
import info from '@/assets/info.png';

export default {
  name: 'MonthlyCalendar',
  components: {
  },
  methods: {
    moment,
  },
  computed: {
    ...mapState(['selectedDate', 'members', 'month']),
    today() {
      return moment();
    },
    weekdays() {
      return [...Array(7)].map((x, i) => moment('2020-01-06').add(i, 'days').format('dddd'));
    },
  },
  data() {
    return {
      info,
    };
  },
  created() {
    moment.locale('es');
  },
};
</script>

